import { Injectable, EventEmitter } from '@angular/core'
import {Observable} from 'rxjs/Observable'
import {Http} from '@angular/http'
import 'rxjs'
import {environment} from '../../environments/environment'

const BASEURL:string = environment.BASEURL + "/api"

@Injectable()
export class AuthService {
  userId
  private user:object
  private userLoginEvent:EventEmitter<any> = new EventEmitter<any>()
  private options = {withCredentials:true}

  constructor(private http: Http) {
    this.isLoggedIn().subscribe()
  }

  public getLoginEventEmitter():EventEmitter<any>{
    return this.userLoginEvent
  }

  public getUser(){
    return this.user
  }

  public emitUserLoginEvent(user){
    this.user = user
    this.userLoginEvent.emit(user)
    return user
  }

    private handleError(e) {
      console.log("AUTH ERROR")
      return Observable.throw(e.json().message)
    }

    signup(username,password, email) {
      console.log("entrooo")
      return this.http.post(`${BASEURL}/signup`, {username,password, email}, this.options)
        .map(res => res.json())
        .map(user => this.emitUserLoginEvent(user))
        .catch(this.handleError)
    }

    login(username,password) {
      return this.http.post(`${BASEURL}/login`, {username,password}, this.options)
        .map(res => res.json())
        .map(user => this.emitUserLoginEvent(user))
        .catch(this.handleError)
    }

    logout() {
      return this.http.get(`${BASEURL}/logout`, this.options)
        .map(res => res.json())
        .map(user => this.emitUserLoginEvent(null))
        .catch(this.handleError)
    }

    isLoggedIn() {
      return this.http.get(`${BASEURL}/loggedin`, this.options)
        .map(res => res.json())
        .map(user => this.emitUserLoginEvent(user))
        .catch(this.handleError)
    }

    editProfile(username,password, email) {
      this.userId = this.getUser()
      console.log("editoooo", this.userId)
      // console.log(this.userId._id)
      return this.http.put(`${BASEURL}/profile/${this.userId._id}/edit`, {username,password, email}, this.options)
        .map(res => res.json())
        .map(response_object => {
          console.log('response:', response_object.user)
          this.emitUserLoginEvent(response_object.user)
        })
        .catch(this.handleError)
    }

}



